<!--  新增、编辑人员 -->
<template>
  <el-dialog :title="userInfo.accountNo?'修改人员':'新增人员'" :visible.sync="show" center width="550px" @close="resetForm('ruleForm')" class="userForm-box" :close-on-click-modal="false">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
      <el-form-item label="账号：" :prop="!userInfo.accountNo?'accountNo':''">
        <template v-if="!userInfo.accountNo">
          <el-input v-model.trim="ruleForm.accountNo" placeholder="请输入账号" class="input-box" clearable></el-input>
        </template>
        <template v-else>
          <p>{{ruleForm.accountNo||EMPTYTEXT}}</p>
        </template>
      </el-form-item>
      <el-form-item label="姓名：" prop="realName">
        <el-input v-model.trim="ruleForm.realName" placeholder="请输入姓名" class="input-box" clearable maxlength="20"></el-input>
      </el-form-item>
      <el-form-item label="工号：" prop="workNum">
        <el-input v-model.trim="ruleForm.workNum" placeholder="请输入工号" class="input-box" clearable maxlength="20"></el-input>
      </el-form-item>
      <el-form-item label="机构名称：" prop="orgList">  
          <treeselect :multiple="true" :options="organOption"  :flat="true" :default-expand-level="1" placeholder="请选择机构" v-model="ruleForm.orgList" class="input-box" @input="changeParentOrgan"/>
      </el-form-item>
      <el-form-item label="性别：" prop="gender">
        <el-select placeholder="请选择性别" v-model="ruleForm.gender" clearable class="input-box">
          <el-option v-for="item in sexOption" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="身份证号：" prop="idCard">
        <el-input v-model.trim="ruleForm.idCard" placeholder="请输入身份证号码" class="input-box" clearable></el-input>
      </el-form-item>
      <el-form-item label="手机号：" prop="mobile">
        <el-input v-model.trim="ruleForm.mobile" placeholder="请输入手机号" class="input-box" clearable maxlength="11"></el-input>
      </el-form-item>
       <el-form-item label="出生日期：" prop="birthday">
         <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="选择出生日期" clearable class="input-box" value-format="yyyyMMdd" :picker-options="pickerOptions"></el-date-picker>
      </el-form-item>
      <el-form-item label="密码：" prop="passwords" v-if="!userInfo.accountNo">
        <el-input v-model.trim="ruleForm.passwords" type="password" placeholder="8~16位，至少包含数字/字母两种组合" clearable/>
      </el-form-item>
      <el-form-item label="确认密码：" prop="checkPasswords" v-if="!userInfo.accountNo">
        <el-input v-model.trim="ruleForm.checkPasswords" type="password" placeholder="请确认密码" clearable/>
      </el-form-item>
      <el-form-item label="状态：" prop="status">
        <el-select placeholder="请选择状态" v-model="ruleForm.status" class="input-box">
          <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="岗位：" prop="userJob">
        <el-input v-model.trim="ruleForm.userJob" placeholder="请输入岗位" class="input-box" clearable></el-input>
      </el-form-item>
       <el-form-item label="职称：" prop="userDuty">
        <el-input v-model.trim="ruleForm.userDuty" placeholder="请输入职称" class="input-box" clearable></el-input>
      </el-form-item>
      <el-form-item label="备注：">
        <el-input v-model.trim="ruleForm.remark" type="textarea" placeholder="请填写备注"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('ruleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
import formRule from "../../../util/formRule" //表单验证
import Treeselect from '@riophae/vue-treeselect' //树型select
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
  name: 'addUser',
  components:{
    Treeselect
  },
  props: {
    value: {
      type: Boolean
    },
    userInfo:{
      type:Object,
      default: ()=>{}
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show===true){
        this.getOrganList();//获取机构下拉
        this.init() //初始化
      }
    }
  },
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.passwords) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
       show:this.value,
       pickerOptions: {
          disabledDate(time) {
            return time && time.getTime() > Date.now();
          }
       },
       sexOption:[
         {
           value: 1,
          label: '男'
        },
        {
          value: 2,
          label: '女'
        },
        {
          value: 0,
          label: '未说明'
        },
        {
          value: 9,
          label: '未知'
        }
      ],//性别下拉
      statusOption:[
        {
          value: 1,
          label: '正常'
        },
        {
          value: 2,
          label: '冻结'
        },
        {
          value: 3,
          label: '禁用'
        }
      ],//状态下拉
       organOption:[],//机构下拉
       orgListJson:[],
       ruleForm:{
          realName:'',//姓名
          workNum:'', //工号
          orgList:[],//机构列表
          gender:'',//性别
          idCard:'',//身份证号
          mobile:'',//手机号
          accountNo:'', //账号
          birthday:'',//出生日期
          passwords:'', //密码
          checkPasswords:'',//确认密码
          status:1,//状态
          userJob:'',//岗位
          userDuty:'',//职称
          remark:''//备注
       },
       rules: {
        accountNo: [
          { required: true, message: "账号不能为空", trigger: "blur" },
          { validator: formRule.validateEAN, trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ],
        orgList: [
          { type:'array',required: true, message: '请选择机构', trigger: 'change' }
        ],
        idCard: [
          { required: true, message: '身份证号不能为空', trigger: 'blur' },
          { validator: formRule.validateIdcard, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '手机号不能为空', trigger: 'blur' },
          { validator: formRule.validateMobile, trigger: 'blur' }
        ],
        passwords: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { validator: formRule.validatePassword, trigger: 'blur' }
        ],
        checkPasswords: [
          { required:true,validator:validatePass, trigger: 'blur' }
        ],
        status: [
          { required:true,message: "请选择状态", trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //获取机构列表
    getOrganList(){
      this.$http.post(process.env.VUE_APP_API+'/service/organization/getOrganization').then(res => {
        if(res.code===200){
        let initArrays = JSON.parse(JSON.stringify(res.data).replace(/organizationId/g, 'id').replace(/name/g, 'label').replace(/childrens/g, 'children'))
          this.organOption = initArrays
        }else{
          this.organOption = []
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //递归查询
    recursion(data, current){
        let result;
        if(!data){
          return;
        }
        for(var i in data){
          var item = data[i];
          if(item.id == current){
              let arr = {}
              arr.organizationId= item.id
              arr.organizationName = item.label
              result = arr
              break;
          }else if(item.children && item.children.length > 0){
              result = this.recursion(item.children,current);
              if(result) return result
          }
        }
        return result
    },
    //触发机构验证
    changeParentOrgan(value){
      this.orgListJson = []
      if(value.length>0){
        value.forEach(item=>{
          this.orgListJson.push(this.recursion(this.organOption,item))
        })
      }
      this.$refs.ruleForm.validateField('orgList')
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let originData = JSON.parse(JSON.stringify(this.ruleForm))
          delete originData.orgList
          originData.orgListJson = JSON.stringify(this.orgListJson)
          if(this.userInfo.accountNo){
            let params = {}
            params = Object.assign({},originData)
            delete params.passwords
            delete params.checkPasswords
            this.editUser(params)
          }else{
            let params = {}
            params = Object.assign({},originData)
            this.addUser(params)
          }
        }
      });
    },
    //添加用户
    addUser(params){
      this.$http.post(process.env.VUE_APP_API+'/manager/user/insertUserByDto',params).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑用户
    editUser(params){
      this.$http.post(process.env.VUE_APP_API+'/manager/user/updateManageUserByDto',params).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化
    init(){
      if(this.userInfo.accountNo){
        for(let key in this.ruleForm){
          this.ruleForm[key] = this.userInfo[key]
        }
        let arr = []
        this.orgListJson = []
        this.userInfo.orgList.forEach(item=>{
          arr.push(item.organizationId)
          let obj = {}
          obj.organizationId = item.organizationId
          obj.organizationName = item.organizationName
          this.orgListJson.push(obj)
        })
        this.ruleForm.orgList = arr
      }else{
        this.initForm();
      }
      this.$nextTick(()=>{
        this.$refs['ruleForm'].clearValidate();
      })
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
      this.ruleForm.orgList = []
      this.ruleForm.status = 1
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
    }
  }
}
</script>

<style lang='less'>
  /* 新增、编辑人员 */
  .userForm-box{
    .el-dialog--center .el-dialog__body{
      .el-form{
        width:400px;
        margin:0px auto;
      }
    }
    .input-box{
      width:300px;
    }
  }
</style>