<!-- 人员管理 -->
<template>
  <div class="list-common-box">
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增人员</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">账号</span>
            <el-input v-model.trim="searchKey.accountNo" placeholder="请输入账号" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">姓名</span>
            <el-input v-model.trim="searchKey.realName" placeholder="请输入姓名" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">身份证</span>
            <el-input v-model.trim="searchKey.idCard" placeholder="请输入身份证号码" clearable></el-input>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="accountNo" label="账号" align="center">
            <template slot-scope="scope">
              {{scope.row.accountNo?scope.row.accountNo:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="realName" label="姓名" align="center">
            <template slot-scope="scope">
              {{scope.row.realName?scope.row.realName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="status" label="状态" align="center">
            <template slot-scope="scope">
              <div v-if="currentAccountNo === scope.row.accountNo">
                 {{scope.row.status!==''?statusOption[scope.row.status]:EMPTYTEXT}}
              </div>
              <div v-else class="link" @click="handleStatus(scope.row)">
                {{scope.row.status!==''?statusOption[scope.row.status]:EMPTYTEXT}}
                <cc-svg-icon icon-class="yellowEdit"></cc-svg-icon>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="idCard" label="身份证号" align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.idCard?scope.row.idCard:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="mobile" label="电话"  align="center">
            <template slot-scope="scope">
              {{scope.row.mobile?scope.row.mobile:EMPTYTEXT}}
            </template>
          </el-table-column>
           <el-table-column prop="orgList" label="所属机构"  align="center" :show-overflow-tooltip="true">
            <template slot-scope="scope" v-if="scope.row.orgList.length>0">
              <span v-for="(item,index) in scope.row.orgList" :key="index">
                 {{item.organizationName}}{{index!==scope.row.orgList.length-1?'，':''}}
              </span>
            </template>
            <template v-else>{{EMPTYTEXT}}</template>
          </el-table-column>
           <el-table-column prop="orgStatus" label="分配角色"  align="right" width="170">
            <template slot-scope="scope" >
              {{scope.row.roleName?'['+scope.row.roleName+']':''}}
              <span class="yellow link" @click="handleRole(scope.row)">
               分配<cc-svg-icon icon-class="yellowEdit" ></cc-svg-icon>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row)"></cc-svg-icon>
              <cc-svg-icon icon-class="maintain" @click="handleView(scope.row)"></cc-svg-icon>
              <cc-svg-icon icon-class="delete" @click="handleDelete(scope.row.accountNo)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
     <!-- 新增.编辑人员信息弹窗 -->
     <addUser v-model="userDialog" :userInfo="userInfo" @refresh="getDatalist"></addUser>
     <!-- 分配角色弹窗 -->
     <selectRole v-model="roleDialog" :userInfo="userInfo" @refresh="getDatalist"></selectRole>
  </div>
</template>

<script>
  import addUser from './addUser' //新增、编辑人员信息
  import selectRole from './selectRole' //分配角色
  export default {
    name: 'organs-list',
    components: {
      addUser,
      selectRole
    },
    data() {
      return {
        currentAccountNo:'' ,//当前用户账号
        statusOption:{
          1:"正常",
          2:"冻结",
          3:"禁用",
        },//管理员状态
        searchKey:{
          accountNo:'',//账号
          realName:'', //姓名
          idCard:'',//身份证号
        },
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [], //人员信息列表
        userDialog:false, //新增编辑机构弹窗
        userInfo:{},//用户信息
        roleDialog:false //分配角色弹窗
      }
    },
    created() {
       this.currentAccountNo = localStorage.getItem("accountNo")?localStorage.getItem("accountNo"):''
       console.log(this.currentAccountNo)
       this.getDatalist() //获取人员信息列表
    },
    methods: {
      //查看详情
      handleView(scope) {
        this.$router.push({
          path: '/basicData-manage/user-manage/userMaintain?id=' + scope.accountNo
        })
      },
      //新增编辑人员
      handleAdd(scope){
        this.userDialog = true
        this.userInfo = scope?scope:{}
      },
      //分配角色弹窗
      handleRole(scope){
        this.roleDialog = true
        this.userInfo = scope?scope:{}
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取人员信息列表
      getDatalist() {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params.projectType = 2
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/manager/user/getUserCollectionByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //人员信息列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      },
      //修改状态弹窗
      handleStatus(scope){
       this.$confirm('是否'+(scope.status===1?'禁用':'启用')+scope.accountNo+'的账号', '提示', {
          confirmButtonText: '确 认',
          cancelButtonText: '取 消',
          customClass:'infoMessage',
          center:true
        }).then(() => {
          this.$http.post(process.env.VUE_APP_API+'/manager/user/updateStatus',{
            accountNo:scope.accountNo,
            status:scope.status===1?3:1
          }).then(res => {
            if(res.code===200){
              this.$message.success('操作成功');
              this.getDatalist() //刷新列表
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        })
      },
      //删除用户
      handleDelete (accountNo) {
        this.$confirm('确定删除此数据，删除后将永久删除', '提示', {
          confirmButtonText: '确 认',
          cancelButtonText: '取 消',
          customClass:'infoMessage',
          center:true
        }).then(() => {
          this.$http.post(process.env.VUE_APP_API+'/manager/user/removeOneUser',{
            accountNo:accountNo
          }).then(res => {
            if(res.code===200){
              this.$message.success('删除成功');
              this.getDatalist() //刷新列表
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        })
      }
    }
  }
</script>
<style lang='less' scoped>
</style>