<template>
  <el-dialog title="分配角色" :visible.sync="show" center width="500px" @close="resetForm('roleForm')" :close-on-click-modal="false">
    <el-form :model="roleForm" :rules="rules" ref="roleForm" class="roleForm">
      <el-form-item>
        <p class="title">姓名：{{userInfo.realName||EMPTYTEXT}}</p>
      </el-form-item>
      <el-form-item prop="roleId">
        <el-radio-group v-model="roleForm.roleId">
          <el-radio v-for="item in roleOption" :key="item.roleId" :label="item.roleId">{{item.roleName}}</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('roleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('roleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
export default {
  name: 'select-role',
  props: {
    value: {
      type: Boolean
    },
    userInfo:{
      type:Object,
      default: ()=>{}
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.getRoleList() //获取角色列表
        this.roleForm.roleId = this.userInfo.roleId?this.userInfo.roleId:''
      }
    }
  },
  data () {
    return {
       show:this.value,
       roleForm:{
         roleId:''
       },
       roleOption:[], //角色下拉
       rules: {
          roleId: [
            { required: true, message: '请选择角色', trigger: 'change' }
          ]
      }
    }
  },
  methods: {
    //获取角色列表
    getRoleList(){
      this.$http.post(process.env.VUE_APP_API+'/login/getUserRoleList',{
        projectType:2
      }).then(res => {
         if(res.code===200){
            this.roleOption = res.data
         }else{
            this.$message.error(res.msg);
          }
       })
       .catch(error => {
         this.$message.error(error);
       });
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = {}
          params.accountNo = this.userInfo.accountNo
          params.newRoleId = this.roleForm.roleId
          params.projectType = 2
          this.$http.post(process.env.VUE_APP_API+'/manager/user/updateManageUserRole',params).then(res => {
            if(res.code===200){
              this.$message.success('分配成功');
              this.show = false
              this.$emit('change', false)
              this.$emit('refresh') //刷新列表
              this.$refs[formName].resetFields();
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        }
      });
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$nextTick(()=>{
        this.$refs[formName].clearValidate();
      })
    }
  }
}
</script>

<style lang='less' scoped>
  /* 分配角色弹窗 */
  .roleForm{
    padding:0px 15px;
   .title{
     	font-size: 22px;
      color: #746651;
      margin-bottom:15px;
   }
   .el-radio-group{
     .el-radio{
       margin-bottom:16px;
     }
   }
  }
</style>